.history_pg .modal-body {
  padding: 120px 60px;
}
.content-pane {
  padding: 70px 55px 30px;
  width: 420px;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #fff;
  border-left: 1px solid #d5dce5;
}

.history {
  width: calc(100% - 420px);
  background-color: #f1f5fb;
  padding: 90px 80px 160px;
  color: #222;
}
.history_cont {
  display: flex;
  padding-left: 80px;
}

.title {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 30px;
}
.title h2 {
  font-family: "Montserrat";
  font-size: 33px;
  margin-right: 30px;
}

.category {
  display: flex;
  align-items: center;
  font-size: 17px;
  color: #b2bcc9;
  font-weight: 600;
  transform: translateY(8px);
}

.category li:first-child {
  color: #222;
  font-weight: 700;
}

.category li {
  cursor: pointer;
  position: relative;
  margin-right: 30px;
}
.category li:last-child::after {
  display: none;
}
.category li::after {
  width: 2px;
  height: 16px;
  background-color: #b2bcc9;
  position: absolute;
  top: 1px;
  right: -16px;
  content: "";
}

.cont_txt {
  background-color: #fff;
  padding: 25px 30px 40px;
}

.cont_txt span {
  color: #4a8aff;
  font-size: 16px;
  font-weight: 500;
  display: block;
  margin-bottom: 6px;
}

.cont_txt strong {
  font-size: 24px;
  font-weight: 900;
  line-height: 1.2;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; /* 여러 줄 자르기 추가 스타일 */
  white-space: normal;
  height: 2.4em;
  text-align: left;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.cont_txt p {
  color: #a0a7ab;
  font-size: 16px;
  margin-top: 15px;
  line-height: 1.3;
}

.content-pane {
  padding: 70px 55px 30px;
  width: 420px;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #fff;
  border-left: 1px solid #d5dce5;
}

.content-pane h3 {
  font-size: 23px;
  font-weight: 700;
  margin-bottom: 20px;
  text-align: left;
}
.tab-content {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: calc(100% - 75px);
}
.tab-content button {
  width: 100%;
  height: 56px;
  font-size: 15px;
  background-color: #22272b;
  border-radius: 6px;
  text-align: center;
  outline: none;
  border: none;
  color: #fff;
}
.tab-content .cont_txt {
  padding: 0;
  padding-top: 20px;
}
.tab-content img {
  border-radius: 12px;
}

.tab-content p {
  margin: 1rem 0;
}

@media (max-width: 1650px) {
  .history li.submenu{
    width: calc((100% - 30px) / 2);
  }
  .history li.submenu:nth-child(3n) {
    margin-right: 30px;
  }
  .history li.submenu:nth-child(2n) {
    margin-right: 0;
  }
  .history {
    width: calc(100% - 380px);
    padding: 90px 40px 160px;
  }
  .content-pane {
    padding: 70px 35px 30px;
    width: 380px;
  }
}
