/* .modal{width: 100%;left: 50%;transform: translateX(-50%);} */
.modal-dialog{max-width: 1200px;width: 100%; position: relative;}
.modal-footer>* {margin: 0;} 
.modal-content {width: 1200px;border:none; border-radius: 2.2rem;}
.modal-header { padding: 0;border-top-left-radius:1rem;border-top-right-radius:1rem;border-bottom: 0;} 
.modal-body { padding: 0} 
.modal-dialog-scrollable .modal-body { -ms-overflow-style: none; /* IE and Edge */scrollbar-width: none; /* Firefox */ } 
.modal-dialog-scrollable .modal-body::-webkit-scrollbar { display:none; } 
.modal .close{
    position: absolute;
    top: 50%;
    right: -100px;
    z-index: 22222;
    width: 70px;
    height: 70px;
    background-color: #4A8AFF;
    opacity: 1;
    border-radius: 18px;
    border-bottom-right-radius: 0;
}
.modal .close img{width: 40px;transform: translate(1px,2px);}
.close.bc01{
    background-color: #E9609F;
}
.close.bc02{
    background-color: #D2AD79;
}
.close.bc03{
    background-color: #3996D1;
}