.sec1 {
  width: 100%;
  height: 100vh;
  background-color: #0c0506;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.sec1 .content {
  width: 65%;
  height: auto;
  position: relative;
}
.sec1 .text_box {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  display: flex;
  height: 100%;
}

.sec1 .text_box .left {
  width: 45%;
  font-family: "Montserrat", sans-serif;
  font-size: 5rem;
  line-height: 1.2;
  transform: translate(-10%, -8%);
  display: flex;
  align-items: center;
}
.sec1 .text_box .right {
  width: 55%;
  display: flex;
  align-items: flex-end;
  transform: translateY(-20%);
}

.sec1 .text_box .right strong {
  font-family: "Montserrat", sans-serif;
  font-size: 1.5rem;
}

.sec1 .text_box .right p {
  max-width: 650px;
  font-size: 1rem;
  font-weight: 300;
  margin-top: 1rem;
}

.sec2 {
  width: 100%;
  height: 100vh;
  background-color: #edeeed;
  display: flex;
  flex-direction: column;
}
.sec2 .bg {
  width: 100%;
  height: 50vh;
  position: relative;
}

.bg_01 {
  width: 505px;
  position: absolute;
  top: 10%;
  left: 0;
}
.bg_02 {
  width: 387px;
  position: absolute;
  top: 55%;
  right: 8%;
}
.bg_03 {
  width: 368px;
  position: absolute;
  bottom: 2%;
  right: 24%;
}
.bg_04 {
  width: 792px;
  position: absolute;
  top: -3%;
  right: -28%;
}
.bg_05 {
  width: 432px;
  position: absolute;
  bottom: 3%;
  left: -12%;
}
.sec2 {
  padding: 0 80px;
  color: #1f30f4;
}
.sec2 .text_cont {
  width: 100%;
  height: 50vh;
  position: relative;
  display: flex;
  padding-top: 10rem;
}
.sec2 .left {
  width: 600px;
}
.sec2 .left strong {
  font-size: 40px;
  font-weight: 700;
  line-height: 1.3;
}
.sec2 .left p {
  margin-top: 0.5rem;
}
.sec2 .right {
  width: 450px;
  font-size: 17px;
  z-index: 12;
}
.sec2 .right p {
  margin-bottom: 1.5rem;
}
.sec2 .text_cont img {
  width: 25%;
  position: absolute;
  bottom: -40%;
  right: 3%;
}

.sec3,
.sec4,
.sec5 {
  background-color: #1f30f4;
  color: #fff;
  padding: 0 80px;
}

.sec3 {
  display: flex;
  align-items: center;
  padding-top: 170px;
}
.sec3 .text_box {
  position: relative;
}
.sec3 .text_box:nth-child(2) {
  width: 570px;
  margin-top: 85px;
}
.sec3 .left {
  width: 50%;
}
.sec3 .left strong {
  font-size: 24px;
  font-weight: 500;
}

.sec3 .left p {
  width: 540px;
  margin-top: 12px;
  font-weight: 400;
  line-height: 1.6;
}

.sec3 > article {
  width: 100%;
  margin-top: 150px;
  display: flex;
  align-items: center;
}
.sec3 .right {
  width: 50%;
  padding-left: 20px;
}
.progress {
  display: flex;
  flex-wrap: wrap;
  max-width: 730px;
}
.progress li {
  width: 122px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px;
  font-size: 17px;
  text-align: center;
}
.progress li span {
  margin-top: 20px;
}
.progress li:nth-child(5) {
  margin-left: 100px;
}
.mark {
  position: absolute;
  top: -145px;
  left: -110px;
  width: 220px;
}

.sec4 {
  padding-top: 160px;
}

.sec4 .text_cont {
  margin-bottom: 80px;
}

.sec4 .text_cont strong {
  font-size: 40px;
}

.sec4 .text_cont p {
  font-size: 18px;
  margin-top: 10px;
  font-weight: 300;
}
.table {
  width: 100%;
}
.table td {
  font-size: 17px;
  height: 66px;
  vertical-align: middle;
  text-align: center;
  padding: 20px 15px;
}
.table td:last-child {
  text-align: right;
  padding-right: 60px;
}
.table tr {
  border-bottom: 1px solid #bfbfbf;
  border-style: dashed;
}

.table tr:nth-child(even) {
  border-bottom: none;
}
.table tr:nth-child(even) td {
  height: 105px;
  vertical-align: text-top;
}
.table tr:nth-child(odd) {
  border-top: 1px solid #fff;
}

.table tr:nth-child(odd) td:first-child {
  position: relative;
}
.table tr:nth-child(odd) td:first-child::after {
  width: 1px;
  height: 12px;
  background-color: #fff;
  content: "";
  position: absolute;
  top: 0;
  right: 0;
}

.sec5 {
  padding-top: 240px;
  position: relative;
  padding-bottom: 120px;
}

.sec5 .mark {
  top: 80px;
  left: -35px;
}

.search {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #fff;
  padding-bottom: 25px;
  margin-bottom: 20px;
}
.search strong {
  font-size: 29px;
}
.search img {
  width: 32px;
}
.search_text {
  display: flex;
}
.search_text p {
  display: flex;
  align-items: center;
  margin-right: 18px;
}
.search_text img {
  width: 28px;
  margin-left: 4px;
  transform: translateY(-2px);
}

.latest_work {
  width: 100%;
  margin-top: 50px;
}
.latest_work li {
  display: flex;
  padding: 65px 0;
  border-bottom: 1px solid #bfbfbf;
  border-style: dashed;
  cursor: pointer;
  position: relative;
}
.latest_work li:hover .sumnail {
  opacity: 1;
}
.latest_work li .sumnail {
  width: 400px;
  position: absolute;
  top: 50%;
  left: 10%;
  transform: translateY(-50%);
  z-index: 2;
  opacity: 0;
  transition: all 0.2s ease-in;
}
.latest_work li:nth-child(2) .sumnail {
  left: 30%;
}
.latest_work li:nth-child(3) .sumnail {
  left: 50%;
}
.latest_work li:last-child {
  border-bottom: none;
}

.latest_work li p {
  font-size: 18px;
  width: 240px;
  flex-shrink: 0;
}
.latest_work li > img {
  width: 400px;
}
.latest_work li span {
  font-size: 18px;
  text-align: right;
  width: 240px;
  flex-shrink: 0;
}
.project_title {
  width: calc(100% - 280px);
  font-size: 80px;
  font-family: "Montserrat", sans-serif;
  padding-right: 12px;
  display: flex;
  align-items: center;
  line-height: 1.2;
}
.project_title img {
  width: 32px;
}

.sec6 {
  width: 100%;
  height: 100vh;
  background-color: #0c0506;
  color: #fff;
  padding: 0 80px;
  display: flex;
  align-items: center;
  position: relative;
}
.txt_top p {
  font-family: "GmarketSans";
  font-size: 28px;
}
.txt_top strong {
  font-family: "Montserrat";
  font-size: 60px;
}

.txt_bottom {
  margin-top: 60px;
}
.txt_bottom span {
  font-family: "GmarketSans";
  font-size: 12px;
}

.txt_bottom p {
  font-family: "GmarketSans";
  font-size: 26px;
  margin-top: 5px;
}

.txt_cont {
  transform: translateY(-40px);
}

.flow_text {
  display: flex;
  flex: 0 0 auto;
  white-space: nowrap;
  overflow: hidden;
  transition: 0.3s;
  font-size: 2.5rem;
  font-weight: bold;
  color: #9d9993;
  transform: translate3d(var(--move-initial), 0, 0);
  animation: marquee 50s linear infinite;
  animation-play-state: running;
}

.flow_wrap {
  padding-right: 1.4881vw;
  font-family: "Montserrat";
  font-size: 240px;
  color: #241e1f;
  position: relative;
  margin-right: 120px;
}

.flow_wrap img {
  width: 220px;
  position: absolute;
  top: 50%;
  right: -155px;
  transform: translateY(-50%);
}

@keyframes marquee {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
}

.flow_container {
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (max-width: 1650px) {
  .progress li {
    width: calc(100% / 3);
    margin: 30px 0;
  }
  .progress li:nth-child(5) {
    margin-left: 0;
  }
}

@media (max-width: 1440px) {
  .sec1 .text_box .left {
    font-size: 4.2rem;
  }
}
