.header {
    width: 80px;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: space-between;
    padding: 30px 0;
}
nav{width: 80px;}
.header_top{display: flex;flex-direction: column; align-items: center;}
.logo {
    width: 30px;
    margin-bottom: 20px;
}
.header li{
    width: 100%;
    height: 50px;
    position: relative;
}
.header a{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header a img{width: 24px;}
.header a:active,
.header a.active {
    border-left: 3px solid #fff;
}
.header .menu2 a:active,
.header .menu2 a.active {
    border-left: 3px solid #4A8AFF;
}
.header a:active img,
.header a.active img{
    transform: translate(-1.5px);
}
.header_bottom{
    width: 45px;
}
.nav_hover{
    position: absolute;
    top: 0;
    right: -75px;
    font-size: 15px;
    background-color: #23272B;
    border-radius: 20px;
    padding: 5px 12px 4px;
    color: #ffffff;
    opacity: 0;
    transition: all 0.2s ease-in;
}

.header li:nth-child(2) .nav_hover{
    right: -104px;
}

.header li:hover .nav_hover{
    opacity: 1;
}